.startExam{
    display: flex;
    width: 100%;

    .startExamContainer{
        flex: 6;

        .titleContainer{
            -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47); 
            box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
            margin: 20px;
            padding: 20px;

            .examTitle{
                font-weight: 500;
                color: gray;
                margin-bottom: 15px;
              }
        }

        .questionContainer{
            -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47); 
            box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
            margin: 20px;
            padding: 20px;

        }
    }
}