.table{
    .cellWrapper{
        display: flex;
        align-items: center;

        .image{
            width: 322px;
            height: 32px;
            border-radius: 50%;
            margin-right: 10px;
            object-fit: cover;
        }
    }

    .status {
        padding: 5px;
        border-radius: 5px;
    
        &.Passed {
          color: green;
          background-color: rgba(0, 128, 0, 0.151);
        }
        &.Failed {
          color: goldenrod;
          background-color: rgba(189, 189, 3, 0.103);
        }
    }
}