.loginInput{
    display: flex;
    flex-direction: column;
    width: 320px;

    input{
        padding: 15px;
        margin: 5px 0px;
        border-radius: 5px;
        height: 10px;
        border: 1px solid gray;
    }
    
    label{
        font-size: 12px;
        color: gray;
    }

    span{
        font-size: 12px;
        padding: 3px;
        color: red;
        display: none;
    }
}


input:invalid[focused="true"]{
    border: 1px solid red;
}

input:invalid[focused="true"] ~ span{
    display: block;
}